import $ from 'jquery'
import createText from './create-text'

$(document).ready(() => {

  /**
   * Adiciona o texto na pgina
   */
  const addText = text => {
    return $('#text').val(text)
  }

  /**
   * Obtem o nmero de pargrafos que o usurio quer
   */
  const getParagraphs = () => {
    return $('#paragraphs').val()
  }

  /**
   * Gerar o texto e o adiciona na pgina
   */
  const generateText = () => {
    addText(createText(getParagraphs()))
  }

  /**
   * Gerar o texto quando o usurio clicar o boto
   */
  $('#generate').on('click', generateText)

  /**
   * Gerar o texto quando a pgina for carregada
   */
  generateText()
})